.container {
  position: relative;
  max-width: 1136px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}
.disable {
  position: relative;
  &::before {
    position: absolute;
    content: '';
    background-color: white;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
  }
}
